import { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import { useDispatch } from "react-redux";
import { newTravelEnquiry } from "../../redux/actions/enquiry";

const Example = ({ show, handleClose, formData }) => {
  const dispatch = useDispatch();
  const [personalData, setPersonalData] = useState({
    fullName: "",
    email: "",
    phone: "",
    tripDuration: 0,
    specialNotes: "",
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setPersonalData({ ...personalData, [name]: value });
  };
  const handleSubmit = () => {
    let data = { ...personalData, ...formData };
    console.log(data);
    data = {
      ...data,
      tripDuration: parseInt(data.tripDuration),
      noOfTravelers: parseInt(data.noOfTravelers),
      budgetPerPerson: parseInt(data.budgetPerPerson),
    };
    dispatch(newTravelEnquiry(data));
  };
  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header>
          <Modal.Title>
            We need a bit more info to create your itinerary:
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Full Name</Form.Label>
              <Form.Control
                name="fullName"
                placeholder="Anshul Sharma"
                onChange={handleChange}
                autoFocus
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                name="email"
                placeholder="name@example.com"
                onChange={handleChange}
                autoFocus
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Phone No.</Form.Label>
              <Form.Control
                name="phone"
                placeholder="##########"
                autoFocus
                onChange={handleChange}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Trip Duration (Days)</Form.Label>
              <Form.Control
                name="tripDuration"
                placeholder="Ex: 10 (days)"
                autoFocus
                onChange={handleChange}
              />
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Special Notes</Form.Label>
              <Form.Control
                name="specialNotes"
                as="textarea"
                rows={3}
                onChange={handleChange}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="outline-red" onClick={handleClose}>
            Close
          </Button>
          <Button
            variant="mdb-color darken-2"
            onClick={() => {
              handleClose();
              handleSubmit();
            }}
          >
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
      {/* </div> */}
    </>
  );
};

export default Example;
