import { NEW_TRAVEL_ENQUIRY, GET_ALL_TRAVEL_ENQUIRY } from "../actions/types";

export default function (state = {}, action) {
  switch (action.type) {
    case NEW_TRAVEL_ENQUIRY:
      return { ...state, travelEnquiry: action.payload };
    case GET_ALL_TRAVEL_ENQUIRY:
      return { ...state, allTravelEnquiry: action.payload };
    default:
      return state;
  }
}
